.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    height: 90px;
    width: 100%;
    z-index: 10;
}

.header-bg{
     background-color: rgba(0,0,0,0.85);
     transition: 0.5s;
}

.nav-menu{
    display: flex;
    background-color: solid black 2px;
}

.nav-menu li{
    padding: 0 1rem;
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 80vh;
        background: rgba(0,0,0,0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
        /* transition: height 0.3s ease-in-out; */
    }

    .nav-menu.active{
        left: 0;
    }

    .nav-menu li{
        padding: 2rem 0;
    }
    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger{
        display: initial; 
        /* default value */
    }
}